.HomeMainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;

    gap: 70px;
    margin-bottom: 20px;
}

.HomeContainer {
    width: 90%;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    

    background: rgba(217, 217, 217, 0.2);
    border-radius: 60px;
}

.HomeContainerWidthDisabled{
    width: 100%;
}

.ExpandedHomeContainer {
    width: 90%;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background: rgba(217, 217, 217, 0.21);
    border-radius: 57px;
}

.HomeContainerSignContainer {
    display: flex;
    flex-direction: column;
    margin: 0px 0px 40px 50px;
}

.HomeContainerMainSign {
    display: flex;
    margin-bottom: 24px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;

    margin: 50px 0px 34px 50px;
}

.HomeContainerSmallSign {
    display: flex;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
}

.HomeContainerButtonsContainer{
    margin: 0px 0px 32px 5%;
    display: flex;
    flex-direction: row;
    padding-left: 5%;

    gap: 10px;
}

.HomeContainerButtonsButtonContainer {
    display: flex;
}

.HomeContainerButtonsButton {
    color: white;
    padding: 7px 40px 7px 40px;
    background-color: transparent;
    border-color: transparent;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    border-radius: 15px;
}

.HomeContainerButtonsButton:hover {
    background-color: rgba(217, 217, 217, 0.7);

    transition-property: background-color;
    transition-duration: 150ms;
    transition-timing-function: ease-in;
}